<!--
 * @Description: 订单列表
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-29 15:19:29
 * @LastEditTime: 2019-10-09 18:19:54
 -->

<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items" />
    <Module2 ref="module2" />
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="order_no" label="用户订单号"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="out_trade_no" label="用户账号"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info(false)"
              menu-props="auto"
              v-model="status"
              :items="agent_list"
              item-text="text"
              item-value="value"
              return-object
              label="订单状态"
              required
            ></v-select>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info(false)">筛选用户</v-btn>
        </v-layout>
      </v-card-title>
      <!--
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <tr @dblclick="info_data( props.item )">
          <td class="text-xs-centre">
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <span color="primary" dark v-on="on">{{ props.item.user_name }}</span>
              </template>
              <span>{{ props.item.user_phone | cover }}</span>
            </v-tooltip>
          </td>
          <td class="text-xs-centre">
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <span color="primary" dark v-on="on">{{ props.item.order_no | slice_10 }}</span>
              </template>
              <span>{{ props.item.order_no }}</span>
            </v-tooltip>
          </td>
          <td class="text-xs-centre">
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <span color="primary" dark v-on="on">{{ props.item.out_trade_no | slice_10 }}</span>
              </template>
              <span>{{ props.item.out_trade_no }}</span>
            </v-tooltip>
          </td>
          <td class="text-xs-centre">{{ (props.item.amt).toFixed(2) }} 元</td>
          <td class="text-xs-centre">
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <span color="primary" dark v-on="on">{{ props.item.doctor_name }}</span>
              </template>
              <span> {{ props.item.doctor_phone | cover }}</span>
            </v-tooltip>
          </td>
          <td class="text-xs-centre">{{ props.item.create_time | time }}</td>
          <td class="text-xs-centre">{{ props.item.update_time | time }}</td>
          <td class="text-xs-centre">
            <span v-if="props.item.expire_date == 0">未激活</span>
            <span v-else>{{ props.item.expire_date | time }}</span>
          </td>
          <td class="text-xs-centre">
            <span v-if="props.item.msg_index.length == 0">未使用</span>
            <span v-else>{{ props.item.msg_index.length }}</span>
          </td>
          <td class="text-xs-centre">
            <v-chip color="" v-if="props.item.status == 3000">订单未支付</v-chip>
            <v-chip color=""  v-if="props.item.status == 3001">订单已关闭</v-chip>
            <v-chip color="green" text-color="white" v-if="props.item.status == 3002">用户支付中</v-chip>
            <v-chip color="red"  text-color="white" v-if="props.item.status == 3003">订单已撤销</v-chip>
            <v-chip color="red" v-if="props.item.status == 3004">转入退款</v-chip>
            <v-chip color="red"  text-color="white" v-if="props.item.status == 3005">支付失败</v-chip>
            <v-chip color="green"  text-color="white" v-if="props.item.status == 3006">支付成功</v-chip>
            <v-chip color="green" text-color="white" v-if="props.item.status == 3010">订单使用中</v-chip>
            <v-chip color="red" text-color="white" v-if="props.item.status == 3011">订单已失效</v-chip>
            <v-chip color="green" text-color="white" v-if="props.item.status == 3012">订单已评价</v-chip>
          </td>
          <td class="text-xs-centre">
            <v-menu
              bottom
              dark
              origin="center center"
              :close-delay="100"
              transition="slide-x-transition"
            >
              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark v-on="on">订单操作</v-btn>
              </template>
              <v-list>
                <v-list-tile  @click="info_data( props.item )">
                  <v-list-tile-title>订单详情</v-list-tile-title>
                </v-list-tile>
                <v-list-tile  @click="manages_list( props.item )">
                  <v-list-tile-title>咨询记录</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </td>
          </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <p class="p_text">没有更多数据了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>
    <!-- 确认框 -->
    <v-dialog v-model="dialog_logout" max-width="290">
      <v-card>
        <v-card-title class="headline">删除操作</v-card-title>
        <v-card-text>确认删除该用户吗？确认删除将不可恢复</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
          <v-btn color="green darken-1" flat="flat" @click="delMessage">确认删除</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module2 from "./components/module2.vue";
export default {
  components: {
    Breadcrumbs,
    Module2
  },
  data() {
    return {
      // 确认框
      dialog_logout: false,
      // 订单状态
       /*
          '订单状态3000订单未支付,
          3001订单已关闭,
          3002用户支付中,
          3003已撤销(刷卡支付),
          3004转入退款,
          3005支付失败(其他原因，如银行返回失败),
          3006支付成功,
          3010订单正在使用,
          3011订单过期或失效,
          3012订单已评价,默认显示所有'
        */
      agent_list: [
        { text: "全部订单",value: 0},
        { text: "订单未支付", value: 3000},
        { text: "订单已关闭", value: 3001},
        { text: "用户支付中", value: 3002},
        { text: "订单已撤销",value: 3003},
        { text: "转入退款",value: 3004},
        { text: "支付失败", value: 3005},
        { text: "支付成功", value: 3006},
        { text: "订单使用中", value: 3010},
        { text: "订单已失效",value: 3011},
        { text: "订单已评价",value: 3012},
      ],
      // 筛选数据
      user_id: 0,
      doctor_id: 0,
      status: {text: "全部",value:0},
      page: 0,
      page_count: 10,
      out_trade_no: this.$route.params.phone == undefined?"":this.$route.params.phone,
      order_no: "",
      // 分页数据
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      allList: 0,
      // 导航数据
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "订单列表",
          disabled: false,
          to: "/order"
        }
      ],
      // 表格数据
      headers: [
        { text: "用户账号", value: "fat", sortable: false },
        { text: "用户订单号", align: "left", sortable: false, value: "name" },
        { text: "商户订单号", value: "carbs", sortable: false },
        { text: "订单金额", value: "calories", sortable: false },
        { text: "医生账号", value: "fat", sortable: false },
        { text: "订单创建时间", value: "protein", sortable: false },
        { text: "订单更新时间", value: "iron", sortable: false },
        { text: "订单过期时间", value: "iron", sortable: false },
        { text: "已发消息次数", value: "iron", sortable: false },
        { text: "订单状态", value: "iron", sortable: false },
        { text: "操作", value: "iron", sortable: false }
      ],
      desserts: [],
      // 权限数据
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  mounted: function() {
    // this.info();

  },
  methods: {
    // 订单详情
    info_data(list) {
      let datas = {
        state: true,
        list: list
      };
      this.$refs.module2.info(datas);
    },
    // 删除订单
    delMessage() {},
    /* 咨询记录 */
    manages_list( manages ){
      if( manages.msg_index.length == 0 ){
        this.$Dialog.getProductCheckResult_({
                text: '此订单暂无咨询记录!',
                timeout: 3000,
                snackbar: true
            })
        return
      }
      this.$router.push({
                path: `/chatList`,
                query:{
                  data: manages
                }
              });
    },
    // 用户列表
    info(event) {
      this.loader = true;
      this.$axios({
          method: "post",
          url: this.$apis.URL.order_list,
          data: {
            user_id: this.user_id,
            doctor_id: this.doctor_id,
            status: this.status.value,
            page: event == false ? this.page : event.page - 1,
            page_count: event == false ? this.page_count : event.rowsPerPage,
            phone: this.out_trade_no,
            order_no: this.order_no
          }
        })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
            this.allList = response.data.datas.total_number;
          } else {
            this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
.v-list__tile__avatar{
  min-width: 100px;
}
</style>


